#root .appointment {
  height: 100%;
}
#root .appointment .footer {
  width: 100%;
  text-align: center;
  position: fixed;
  bottom: 0;
  background: #fff;
}
#root .appointment .footer > span {
  padding: 10px 0;
  width: 33.3333333%;
  border: 1px solid transparent;
}
#root .appointment .footer > span.active {
  color: #74BA60;
  border-color: #74BA60;
}
