#root .main > div {
  background-color: #fff;
  margin-bottom: 20px;
}
#root .main .title {
  padding: 10px 0;
  text-align: center;
}
#root .main .title > a {
  color: #000;
  box-shadow: 1px 0px 0px 0px #ccc;
  width: 33.333333%;
}
#root .main .title > a > div {
  margin-top: 10px;
}
#root .main .msg {
  padding: 5px;
  align-items: center;
}
#root .main .msg > div {
  width: 20%;
  text-align: center;
}
#root .main .msg > ul {
  padding: 0;
}
#root .main .msg > ul li {
  margin-bottom: 10px;
}
#root .main .msg > ul li > span:first-child {
  border: 1px solid #74BA60;
  color: #74BA60;
  border-radius: 5px;
  padding: 0 5px;
  margin-right: 10px;
}
#root .main h5 {
  padding-left: 20px;
  color: #74BA60;
}
