html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  background-color: #F2F2F2;
}
.flex {
  display: -webkit-flex;
  display: flex;
}
.flex-btw {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.flex-ar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.unstyle {
  list-style: none;
}

#root .main > div {
  background-color: #fff;
  margin-bottom: 5.33333vw;
}
#root .main .title {
  padding: 2.66667vw 0;
  text-align: center;
}
#root .main .title > a {
  color: #000;
  box-shadow: 1px 0px 0px 0px #ccc;
  width: 33.333333%;
}
#root .main .title > a > div {
  margin-top: 2.66667vw;
}
#root .main .msg {
  padding: 1.33333vw;
  -webkit-align-items: center;
          align-items: center;
}
#root .main .msg > div {
  width: 20%;
  text-align: center;
}
#root .main .msg > ul {
  padding: 0;
}
#root .main .msg > ul li {
  margin-bottom: 2.66667vw;
}
#root .main .msg > ul li > span:first-child {
  border: 1px solid #74BA60;
  color: #74BA60;
  border-radius: 1.33333vw;
  padding: 0 1.33333vw;
  margin-right: 2.66667vw;
}
#root .main h5 {
  padding-left: 5.33333vw;
  color: #74BA60;
}

#root .main > .nav {
  display: -webkit-flex;
  display: flex;
  background-color: #000;
}
#root .main > .nav > div {
  width: 80%;
}
#root .main > .nav .am-button {
  width: 20%;
  background: #000;
  color: #fff;
}
#root .main > .nav .am-button::before {
  border: none;
}
#root .main > .nav .am-list-item {
  background-color: #000;
}
#root .main > .nav .am-list-item .am-list-extra {
  color: #fff;
  text-align: left;
}

#root .home {
  height: 100%;
}
#root .home .am-tabs {
  height: calc(100% - 13.33333vw);
}
#root .home .am-tab-bar-item {
  height: auto;
}
#root .home .am-tabs-tab-bar-wrap {
  width: 100%;
  position: fixed;
  bottom: 0;
}
#root .home .am-tabs-tab-bar-wrap .am-tab-bar-bar {
  -webkit-align-items: center;
          align-items: center;
  padding: 2.66667vw 0;
}
#root .home .am-tabs-tab-bar-wrap .am-tab-bar-bar P {
  margin: 0;
}

#root .appointment {
  height: 100%;
}
#root .appointment .footer {
  width: 100%;
  text-align: center;
  position: fixed;
  bottom: 0;
  background: #fff;
}
#root .appointment .footer > span {
  padding: 2.66667vw 0;
  width: 33.3333333%;
  border: 1px solid transparent;
}
#root .appointment .footer > span.active {
  color: #74BA60;
  border-color: #74BA60;
}

