#root .home {
  height: 100%;
}
#root .home .am-tabs {
  height: calc(100% - 50px);
}
#root .home .am-tab-bar-item {
  height: auto;
}
#root .home .am-tabs-tab-bar-wrap {
  width: 100%;
  position: fixed;
  bottom: 0;
}
#root .home .am-tabs-tab-bar-wrap .am-tab-bar-bar {
  align-items: center;
  padding: 10px 0;
}
#root .home .am-tabs-tab-bar-wrap .am-tab-bar-bar P {
  margin: 0;
}
